.table {
  width: 100%;
}

tbody tr:nth-child(even) {
  background-color: var(--light-grey-bg-color);
}

.head {
  color: var(--header-text-color);
}

.body {
  color: var(--primary-text-color);
  box-shadow: 0px 1px 4px 0px #3c348726;
}

.filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.check {
  width: 10px;
}

.checkbox {
  fill: var(--header-text-color);
  width: 10px;
  height: 10px;
}

th {
  text-align: left;
  padding: 8px 0px 8px 24px;
  font-weight: 400;
  font-size: 14px;
}

.iconSort {
  fill: var(--filter-text-color);
  width: 13px;
  height: 13px;
}

.iconDown {
  fill: var(--filter-text-color);
  width: 10px;
  height: 10px;
  margin-right: 21px;
  cursor: pointer;
}

.iconInfo {
  fill: var(--filter-text-color);
  width: 13px;
  height: 13px;
  margin-right: 7px;
}

.infoLine {
  display: flex;
  align-items: center;
}

.page {
  margin-right: 10px;
  font-size: 14px;
  color: var(--dark-grey-bg-color);
}

.footer {
  padding: 18px 0px 18px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.iconRight {
  margin-left: 40px;
  fill: var(--dark-grey-bg-color);
  width: 10px;
  height: 16px;
  cursor: pointer;
}

.iconLeft {
  margin-left: 20px;
  fill: var(--dark-grey-bg-color);
  width: 10px;
  height: 16px;
  cursor: pointer;
}

.iconStat {
  fill: var(--filter-text-color);
  width: 10px;
  height: 10px;
  margin-right: 8px;
  cursor: pointer;
  padding: 12px;
  background-color: #f2f2f2;
}

.flexIcon {
  display: flex;
  align-self: center;
  justify-content: center;
}

.iconDownStat {
  fill: var(--filter-text-color);
  width: 10px;
  height: 10px;
  margin-right: 15px;
  cursor: pointer;
  padding: 12px;
}

.actions {
  margin-right: 15px;
}

.addTable {
  padding: 0;
}

.archStr {
  text-transform: uppercase;
  background-color: var(--main-bg-color);
  box-shadow: 0px 1px 4px rgba(60, 52, 135, 0.15);
  border-top: 1px solid #c0c0c0;
}
